<template>
    <div>
        <el-breadcrumb class="page-hd-breadcrumb" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/exam-admin' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <h1 class="page-title"></h1>
        <span></span>
        <el-table :data="questions">
            <el-table-column label="" prop="name"></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <span v-if="scope.row.type==0"></span>
                    <span v-if="scope.row.type==1"></span>
                    <span v-if="scope.row.type==2"></span>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <span>{{findCourse(scope.row.courseId)['name']}}</span>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot="header">
                    <el-button round size="mini" type="primary" icon="el-icon-upload2" @click="submitAll"></el-button>
                    <el-popconfirm
                            title=""
                            @onConfirm="clearAll"
                    >
                        <el-button slot="reference" round size="mini" type="danger" icon="el-icon-delete"></el-button>
                    </el-popconfirm>
                </template>
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleQuestionEdit(scope.$index, scope.row)"
                            :class="{'btn-hidden':editingFlag}"
                    ></el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleQuestionDelete(scope.$index)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-button :class="{'btn-hidden':!editingFlag}" @click="submitEdit" type="primary"></el-button>
        <el-button :class="{'btn-hidden':!editingFlag}" @click="cancelEdit"></el-button>
        <SimpleQuestionAdd :submit-form="submitForm" :hidden="editingFlag" ref="questionForm"/>
    </div>
</template>

<script>
    import SimpleQuestionAdd from "./SimpleQuestionAdd";
    export default {
        name: "SimpleQuestionAdmin",
        components: {SimpleQuestionAdd},
        data() {
            return {
                questions:[

                ],
                editingFlag:false,
                beforeEditionQuestion:{}
            }
        },
        methods: {
            submitAll(){
                if(this.questions) {
                    this.axios.post('/question/multi', JSON.stringify(this.questions))
                        .then((response) => {
                            if (response['status'] === 200) {
                                this.$message('' + response.data.data)
                                console.log(response)
                                this.clearAll()
                            } else {
                                this.$message.error('')
                            }
                        }).catch((error) => {
                        this.$message.error(": -> " + error.message)
                    })
                }
            },
            clearAll(){
                this.questions=[]
                this.$refs.questionForm.selectedknowledgePoints=[]
            },
            submitForm(){
                // console.log('add a question temporarily')
                if (this.$refs.questionForm.beforeSubmit()) {
                    this.questions.push(this.$refs.questionForm.question)
                    console.log(this.questions)
                    this.editingFlag = false
                    this.$refs.questionForm.resetForm()
                    this.$refs.questionForm.selectedknowledgePoints=[]
                }
            },
            handleQuestionEdit(index,item){
                // console.log(item)
                this.$refs.questionForm.editFormFromMulti(item)
                this.handleQuestionDelete(index)
                this.editingFlag=true
                this.beforeEditionQuestion={...item}
            },
            handleQuestionDelete(index){
                this.questions.splice(index,1)
            },
            submitEdit(){
                this.submitForm()
            },
            cancelEdit(){
                this.$refs.questionForm.editFormFromMulti(this.beforeEditionQuestion)
                this.submitForm()
            },
            findCourse(courseId){
                let arr= this.$refs.questionForm.courses.filter(item=>item.categoryId==courseId)
                return arr[0]
            }
        }
    }
</script>

