                                    @close="removePermission(item)">
                                <!-- remove a permission in a role: -->
                                {{item.permission}}
                            </el-tag>
                            <el-button circle size="mini" type="primary" icon="el-icon-plus"
                                       @click="addPermission(scope.$index)"></el-button>
                        </template>
                    </el-table-column>
                    <el-table-column
                            label=""
