                        "updateTime": "2020-10-24T04:36:22.726Z"
                    },
                ],
                queriedPermissions: [
                    {
                        "addTime": "2020-10-24T04:46:14.354Z",
                        "deleted": false,
                        "permission": "",
                        "permissionId": 0,
                        "roleId": 0,
                        "updateTime": "2020-10-24T04:46:14.355Z"
                    },
                    {
                        "addTime": "2020-10-24T04:46:14.354Z",
